import uniCountdown from "../../components/uni-countdown.vue"
let page = 1

export default {
	data () {
		return {
			swiper_height: '150px',
			headerPosition: 'fixed',
			speakerIconImg: '../../static/images/img/redian.png',
			swiperItems: [
				{
					"imgUrl": "/static/images/tabbar/banner-10.jpg",
					"path": "跳转页面路径",
					"title": "标题信息",
					"openType": "跳转模式"
				},
				{
					"imgUrl": "/static/images/tabbar/banner-9.jpg",
					"path": "跳转页面路径",
					"title": "标题信息",
					"openType": "跳转模式"
				}
			],
			goodsList: [],
			goodsinfo: {},
			loadingType: 0,
			countDown:{
				hour: 0,
				minute: 0,
				second: 10
			},
			xm: 0,
			isShow: []
		}
	},

	components: {
		uniCountdown
	},

	onLoad () {
		page = 1
		this.loadingType = 0
		this.goodsList = []
		this.getgoodlst()
		this.gethomeInfo()
	},

	onShow () {
		if(uni.getStorageSync('token')) {
			this.$store.dispatch('contentnum')
		}
	},

	methods: {
		timeuptime () {
			page = 1
			this.loadingType = 0
			this.goodsList = []
			this.getgoodlst()
			this.gethomeInfo()
		},

		getgoodlst () {
			this.$http.getgoodlst({page:page}).then(res=>{
				if(res.status == 200) {
					if(res.data.length == 0) {
						this.loadingType = 2
						return false
					}
					this.goodsList = [...this.goodsList,...res.data]
					this.loadingType = 0
				}
			})
		},

		gethomeInfo () {
			this.$http.gethomeInfo().then(res=>{
				if(res.status == 200) {
					const date  = new Date(Number(res.data.hdinfos.hdtime) * 1000)
					const getHours = date.getHours() > 9 ? date.getHours() : '0' + date.getHours()
					let str = Number(res.data.hdinfos.end_time) - Number(res.data.hdinfos.dqtime)
					this.xm = getHours
					let  strArr = this.$formatSeconds(str).split('-')
					this.countDown = {
						hour: parseInt(strArr[0]),
						minute: parseInt(strArr[1]),
						second: parseInt(strArr[2])
					}   
				}
			})
		},

		getTab (src) {
			uni.switchTab({
				url: src
			})
		},

		getsing(){
			if (!uni.getStorageSync('token')) {
				this.getnav('/pages/loginSon/login?islogin')					
			}else {
				this.getnav('/pages/personalSon/signin')
			}
		}
	},

	onPullDownRefresh(){
		page = 1
		this.loadingType = 0
		this.goodsList = []
		this.getgoodlst()
		this.gethomeInfo()
	},

	onNavigationBarSearchInputClicked () {
		this.getnav('/pages/homeSon/searchAll?search_id=1')
	},

	onNavigationBarButtonTap (params) {
		if (params.index === 0) {
			this.getnav('/pages/homeSon/classification')
		} else {
			this.getnav('/pages/personalSon/ChatList')
		}
	},

	onReachBottom () {
		if(this.loadingType == 1 || this.loadingType == 2) {
			return false
		}

		this.loadingType = 1

		page++
		this.getgoodlst()
	}
}
